<template>
  <div>
    <div class="container-fluid ">
        <div class="d-block header-section">
            <h1>
            <b>Print Order Details</b>
            </h1>
        </div>
        <!-- START: Card Data-->
		<div class="row">
			<div class="col-12 col-md-12 mt-3">				
				<div class="card mb-4">
					<div class="row p-5">
						<div class="col-12 col-md-4 p-3 row mob-c">
							<div class="col-md-3"><img src="@/assets/images/icons/order-one.png" class="mob-img"/></div>
							<div class="col-md-9 mt-2">
							<h5 class="text-muted">Purchase Order Number</h5>
							<h5 class="d-flex align-items-left text-primary">{{order.poNum}}</h5></div>
						</div>
						<div class="col-12 col-md-4 border-left p-3 row mob-c">
							<div class="col-md-3"><img src="@/assets/images/icons/order-two.png" class="mob-img"/></div>
							<div class="col-md-9 mt-2">
							<h5 class="text-muted">Print Order Number</h5>
							<h5 class="d-flex align-items-left text-primary">{{order.number}}</h5>
							</div>
						</div>
						<div class="col-12 col-md-4 border-left p-3 row mob-c">
							<div class="col-md-3"><img src="@/assets/images/icons/order-three.png" class="mob-img"/></div>
							<div class="col-md-9 mt-2">
							<h5 class="text-muted">Warehouse Name</h5>
							<h5 class="d-flex align-items-left text-primary">{{order.warehouseName}}</h5>
							</div>
						</div> 
						<div class="col-12 col-md-4  p-3 row mob-c">
							<div class="col-md-3"><img src="@/assets/images/icons/order-four.png" class="mob-img"/></div>
							<div class="col-md-9 mt-2">
							<h5 class="text-muted">Print Partner Name</h5>
							<h5 class="d-flex align-items-left text-primary">{{order.printPartnerName}}</h5>
							</div>
						</div>
						<div class="col-12 col-md-4 border-left p-3 row mob-c">
							<div class="col-md-3"><img src="@/assets/images/icons/order-five.png" class="mob-img"/></div>
							<div class="col-md-9 mt-2">
							<h5 class="text-muted">Expected Date</h5>
							<h5 class="d-flex align-items-left text-primary">{{order.expectedDate}}</h5>
							</div>
						</div> 
						<div class="col-12 col-md-4 border-left p-3 row">
							<h5 class="d-flex align-items-left text-primary"> </h5>
						</div> 
					</div>
				</div>

				<div class="card">
					<div class="table-responsive">
					<table id="tab" class="table order">
						<thead>
							<tr>
                                <th>Product Name</th>
                                <th>No. of Orders</th>
                                <th>Reel Size</th>
                                <th>No. of Reels</th>
                                <th>Total Stamps</th>
							</tr>
						</thead>
						<tbody>
                            <tr>
                                <td>{{order.productName}} </td>
                                <td>{{order.noOfOrders}}</td>
                                <td>{{order.reelSize}}</td>
                                <td>{{order.noOfReels}}</td>
                                <td>{{order.totalStamps}}</td>
							</tr>
						</tbody>
					</table>
					</div>
				</div>
				<!-- START: Card Data-->
				<div class="row">
					<div class="col-12 col-sm-12 mt-3">
						<p class="text-muted mb-2">History</p>
						<div class="table-responsive">
							<table class="table layout-secondary bordered">
								<thead>
									<tr>
										<th>Date</th>
										<th>Actioned By</th>
										<th>Action</th>
										<th>Comments</th>
									</tr>
								</thead>
								<tbody>
									<tr style="background: #fff;" v-for="history in order.printOrderHistories" :key="history.id">
										<td>{{history.modifiedDate}}</td>
										<td>{{history.actionedBy}}</td>
										<td>{{history.action}}</td>
										<td>{{history.comments}}</td> 
									</tr>
								</tbody>
							</table> 
						</div>
					</div>
                    <div class="col-md-12 mb-5" v-if="((this.role == 'PrintPartner' && this.printOrderStatus == 'Submitted') || (this.role == 'TsspAdmin' && this.printOrderStatus == 'InTransit'))">
                        <p class="mt-4">Comments</p>
                        <div>
                            <textarea class="form-control" rows="5" v-model="comments"></textarea>
                        </div>
                    </div>
				</div>
				<!-- END: Card DATA-->
				<div class="mb-2">
                    <b-button v-b-modal.modal-sm
                        class="float-right mt-2 mb-3 ml-2 p-2 pl-5 pr-5"
                        variant="success"
                        @click="approveOrder"
                        v-if="this.role == 'PrintPartner' && this.printOrderStatus == 'Submitted'"
                        :disabled="!this.comments"
                    >
                        APPROVE
                    </b-button>
                    <b-button v-b-modal.modal-sm
                        class="float-right mt-2 mb-3 ml-2 p-2 pl-5 pr-5"
                        variant="danger"
                        @click="rejectOrder"
                        v-if="this.role == 'PrintPartner' && this.printOrderStatus == 'Submitted'"
                        :disabled="!this.comments"
                    >
                        REJECT
                    </b-button>
                    <b-button v-b-modal.modal-sm
                        class="float-right mt-2 mb-3 ml-2 p-2 pl-5 pr-5"
                        variant="danger"
                        @click="closeOrder"
                        v-if="this.role == 'TsspAdmin' && this.printOrderStatus == 'InTransit'"
                        :disabled="!this.comments"
                    >
                        CLOSE ORDER
                    </b-button>
                    <b-button v-b-modal.modal-sm
                        class="float-right mt-2 mb-3 p-2 pl-5 pr-5"
                        variant="outline-primary"
                        @click="redirectToHome"
                    >
                        BACK
                    </b-button>
                </div>
			</div>
		</div>
      <!-- END: Card DATA-->
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
        order: null,
        role: "",
        comments: ""
    };
  },
  props:['printOrderStatus','printOrderId'],
  mounted(){
	axios.get(process.env.VUE_APP_OrderMgmtApisBaseUrl + "/PrintOrder?printOrderId=" + this.printOrderId)
	.then(response=>{
		this.order = response.data;
    });
    this.role=this.$store.state.clientAppDecodedToken.Role;
  },
  components: {},
  methods: {
    redirectToHome() {
        this.$router.push({ name: 'PrintOrders' });
    },
    approveOrder() {
        axios.post("/PrintOrder/ApprovePrintOrder",{
                orderId: this.printOrderId,
                comments: this.comments
            },
            {
                baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
            }
        ).then(() => {
            this.$router.push({ name: 'PrintOrders' });
        });
    },
    rejectOrder() {
        axios.post("/PrintOrder/RejectOrder",{
                orderId: this.printOrderId,
                comments: this.comments
            },
            {
                baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
            }
        ).then(() => {
            this.$router.push({ name: 'PrintOrders' });
        });
    },
    closeOrder() {
        axios.post("/PrintOrder/CloseOrder",{
                orderId: this.printOrderId,
                comments: this.comments
            },
            {
                baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl
            }
        ).then(() => {
            this.$router.push({ name: 'PrintOrders' });
        });
    }
  }
};
</script>
